*{padding:0;margin:0;box-sizing:border-box}body{font-family:'微軟正黑體'}nav,footer,main,figure,article,section{display:block}.fix-menu{position:fixed;bottom:30%;right:12px;z-index:888;transition:all .3s;opacity:0}.fix-menu.active{opacity:1}.fix-menu .fix-menu-slogan{width:100px;padding:.35rem 0;text-align:center;background-color:#fff;position:relative;cursor:pointer}.fix-menu .fix-menu-slogan:before{content:'';width:100px;height:0;border-top:7px solid #a20538;border-left:8px solid transparent;border-right:8px solid transparent;position:absolute;bottom:-6px;left:0px;z-index:6}.fix-menu ul{list-style:none;margin:6px auto;width:86px}.fix-menu ul li{width:100%;cursor:pointer;text-align:center;background-color:#ffb85c;border-bottom:1px solid #fff;padding:.35rem 0}.fix-menu ul li:last-child{border-bottom:none}.fix-menu .fix-menu-top{width:100px;padding:.35rem 0;text-align:center;background-color:#fff;cursor:pointer;position:relative;font-size:.9rem}.fix-menu .fix-menu-top:before{content:'';width:100px;height:0;border-bottom:7px solid #a20538;border-left:8px solid transparent;border-right:8px solid transparent;position:absolute;top:-6px;left:0px;z-index:6}.to-top{position:fixed;right:10%;bottom:10%;width:80px;height:80px;background-color:rgba(0,0,0,0.3);border-radius:50%;border:5px solid #fff;display:flex;flex-direction:column;justify-content:center;align-items:center;cursor:pointer;transition:all .5s;opacity:0;transform:scale(0);z-index:999}.to-top.active{opacity:1;transform:scale(1)}.to-top .triangle{border-left:12px solid transparent;border-right:12px solid transparent;border-bottom:12px solid #fff;margin-bottom:.55rem}.to-top span{color:#fff;font-size:1.25rem;font-weight:bold;font-family:Arial, Helvetica, sans-serif;pointer-events:none}#app{width:100%;overflow:hidden;height:600px;background-color:antiquewhite}header{width:100%;max-width:1200px;margin:0 auto;position:relative;z-index:1;overflow:hidden}header .h1-seo{position:absolute;top:0;left:0;transform:translate(-1000%)}#main{width:100%}.item-wrap{width:100%;max-width:1200px;margin:0 auto;position:relative;z-index:1}footer{padding:1rem;text-align:center;background-color:#000;color:#fff}@media screen and (max-width: 990px){footer{font-size:.9rem;padding:.9rem}}@media screen and (max-width: 768px){footer{font-size:.8rem;padding:.8rem}}@media screen and (max-width: 576px){footer{font-size:.7rem;padding:.7rem}}@media screen and (max-width: 414px){footer{font-size:.6rem;padding:.6rem}}
